riablesTab.vue"],"names":[],"mappings":";AA+JA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,eAAe;IACf,qBAAqB;IACrB,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,eAAe;IACf,qBAAqB;IACrB,gBAAgB;IAChB,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,YAAY;IACZ,YAAY;IACZ,+CAA+C;IAC/C,yBAAyB;IACzB,8BAA8B;IAC9B,yBAAyB;AAC7B;AAEA;IACI,kBAAkB;IAClB,cAAc;AAClB;AACA;IACI,kBAAkB;IAClB,YAAY;IACZ,QAAQ;IACR,UAAU;IACV,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,QAAQ;IACR,eAAe;IACf,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,UAAU;IACV,eAAe;IACf,+CAA+C;IAC/C,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,qBAAqB;IACrB,iBAAiB;IACjB,YAAY;IACZ,YAAY;IACZ,iBAAiB;AACrB;AAEA;IACI,YAAY;IACZ,iBAAiB;IACjB,oBAAoB;IACpB,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,YAAY;IACZ,aAAa;IACb,gBAAgB;IAChB,kBAAkB;AACtB;AAEA;IACI,cAAc;IACd,eAAe;IACf,eAAe;AACnB","sourcesContent":["<template>\n    <p class=\"title\">Advanced Variables</p>\n    <p class=\"variable-count\">{{ itemsLength }} items</p>\n    <input class=\"new-variable-input\" v-model=\"newVariableName\" placeholder=\"Variable name\" @input=\"checkVariableNameExists\" />\n    <button :class=\"'new-variable-button' + (variableAlreadyExists ? ' disabled' : '')\" @click=\"addNewVariable\">\n        <i class=\"mdi mdi-plus plus-icon\"></i>\n        <div class=\"add-new-text\">ADD NEW</div>\n    </button>\n    <div class=\"data-table\">\n        <v-client-table ref=\"table\" :data=\"dataTableRows\" :columns=\"dataTableColumns\" :options=\"dataTableOptions\">\n            <template #parameter=\"props\"> {{ props.row.parameter }} </template>\n            <template #currentValue=\"props\">\n                <input\n                    v-if=\"props.row.dataType === 'STRING' || props.row.dataType === 'NUMBER'\"\n                    :type=\"props.row.dataType === 'STRING' ? 'text' : 'number'\"\n                    :id=\"props.row.parameter\"\n                    :name=\"props.row.parameter\"\n                />\n                <select v-if=\"props.row.dataType === 'BOOLEAN'\" v-model=\"props.row.currentValue\">\n                    <option selected>false</option>\n                    <option>true</option>\n                </select>\n                <select v-if=\"props.row.dataType === 'SCREEN'\" v-model=\"props.row.currentValue\" @change=\"handleScreen($event, props.row)\">\n                    <option v-for=\"screen in processVariablesProps.modelsOfModule.screens\" :id=\"screen.ID\">\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <select v-if=\"props.row.dataType === 'HTTP METHOD'\" v-model=\"props.row.currentValue\">\n                    <option v-for=\"restMethod in restMethods\">{{ restMethod }}</option>\n                </select>\n            </template>\n            <template #dataType=\"props\">\n                <select @change=\"handleDataTypeChange($event, props.row)\">\n                    <option v-for=\"variableType in variableTypes\" :selected=\"props.row.dataType === variableType\">\n                        {{ variableType }}\n                    </option>\n                </select>\n            </template>\n            <template #defaultValue=\"props\">\n                {{ props.row.defaultValue }}\n            </template>\n            <template #operations=\"props\">\n                <i\n                    :class=\"'operation-icon mdi' + (props.row.isDeletable ? ' mdi-delete-outline' : ' mdi-arrow-u-left-top')\"\n                    @click=\"props.row.isDeletable ? deleteVariable(props.row) : returnToDefaultValue(props.row)\"\n                />\n            </template>\n        </v-client-table>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ProcessVariable } from \"@stechquick/algae/lib/qCloudTemp/process\";\nimport { ref, computed } from \"vue\";\nimport { ILocalization } from \"../../../../../domain/infrastructure/ILocalization\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModelsOfModule } from \"../../../../../domain/presentation/editors/IProcessEditorModel\";\n\ninterface IProcessVariablesProps {\n    editable?: boolean;\n    variables: Array<ProcessVariable>;\n    modelsOfModule: IModelsOfModule;\n}\n\ninterface IProcessVariableRow {\n    parameter: ProcessVariable[\"key\"];\n    currentValue: ProcessVariable[\"value\"];\n    dataType: ProcessVariable[\"type\"];\n    defaultValue: ProcessVariable[\"value\"];\n    defaultType: ProcessVariable[\"key\"];\n    isDeletable: ProcessVariable[\"isDeletable\"];\n}\n\nconst processVariablesProps = defineProps<IProcessVariablesProps>();\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst variableTypes = ref([\"STRING\", \"BOOLEAN\", \"SCREEN\", \"NUMBER\", \"HTTP METHOD\"]);\nconst restMethods = [\"GET\", \"POST\", \"PUT\", \"PATCH\", \"DELETE\", \"HEAD\", \"CONNECT\", \"OPTIONS\", \"TRACE\"];\n\nconst newVariableName = ref(\"\");\nconst variableAlreadyExists = ref(false);\nconst itemsLength = computed(() => dataTableRows.value.length);\nconst dataTableRows = ref(Array<IProcessVariableRow>());\n\nconst dataTableColumns = ref([\"parameter\", \"dataType\", \"currentValue\", \"defaultValue\", \"operations\"]);\n\nconst dataTableOptions = ref({\n    resizableColumns: false,\n    filterable: false,\n    perPage: Number.MAX_SAFE_INTEGER,\n    perPageValues: [],\n    headings: {\n        parameter: localization.get(\"parameter\"),\n        dataType: localization.get(\"dataType\"),\n        currentValue: localization.get(\"currentValue\"),\n        defaultValue: localization.get(\"defaultValue\"),\n        operations: \"\",\n    },\n    sortable: [],\n    texts: {\n        count: \"\",\n    },\n});\n\ndataTableRows.value = processVariablesProps.variables.map<IProcessVariableRow>((variable) => {\n    const row: IProcessVariableRow = {\n        parameter: variable.key,\n        currentValue: variable.value,\n        dataType: variable.type,\n        defaultValue: variable.value,\n        defaultType: variable.type,\n        isDeletable: variable.isDeletable,\n    };\n    return row;\n});\n\nfunction checkVariableNameExists() {\n    variableAlreadyExists.value = dataTableRows.value.find((row) => row.parameter == newVariableName.value) != undefined;\n}\n\nfunction addNewVariable() {\n    const variable: IProcessVariableRow = {\n        parameter: newVariableName.value,\n        currentValue: \"\",\n        dataType: \"STRING\",\n        defaultValue: \"\",\n        defaultType: \"\",\n        isDeletable: true,\n    };\n    if (variableAlreadyExists.value || newVariableName.value.length == 0) return;\n\n    dataTableRows.value.push(variable);\n    newVariableName.value = \"\";\n}\n\nfunction deleteVariable(row: IProcessVariableRow) {\n    dataTableRows.value = dataTableRows.value.filter((dataTableRow) => dataTableRow.parameter != row.parameter);\n}\n\nfunction returnToDefaultValue(row: IProcessVariableRow) {\n    const currentRowIndex = dataTableRows.value.findIndex((dataTableRow) => dataTableRow.parameter == row.parameter)\n    dataTableRows.value[currentRowIndex].currentValue = dataTableRows.value[currentRowIndex].defaultValue;\n    dataTableRows.value[currentRowIndex].dataType = dataTableRows.value[currentRowIndex].defaultType;\n}\n\nfunction handleDataTypeChange($event: any, row: IProcessVariableRow) {\n    const currentRowIndex = dataTableRows.value.findIndex((dataTableRow) => dataTableRow.parameter == row.parameter)\n    dataTableRows.value[currentRowIndex].currentValue = \"\";\n    dataTableRows.value[currentRowIndex].dataType = $event.target.value\n}\n\nfunction handleScreen($event: any, row: IProcessVariableRow) {\n    const selectedScreen = $event.target[$event.target.selectedIndex];\n    const currentRow = dataTableRows.value.find((dataTableRow) => dataTableRow.parameter == row.parameter);\n    if (!currentRow) return;\n    currentRow.currentValue = selectedScreen.id;\n}\n</script>\n\n<style scoped>\n.title {\n    position: absolute;\n    top: 118px;\n    left: 32px;\n    font-size: 24px;\n    font-family: \"Roboto\";\n    color: #212121;\n}\n\n.variable-count {\n    position: absolute;\n    top: 128px;\n    left: 255px;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    font-weight: 500;\n    color: #a1a1a1;\n}\n\n.new-variable-input {\n    position: absolute;\n    top: 118px;\n    left: 647px;\n    width: 220px;\n    height: 32px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px 0px 0px 4px;\n    padding: 2px 0px 7px 10px;\n}\n\n.new-variable-input::placeholder {\n    position: absolute;\n    color: #a1a1a1;\n}\n.plus-icon {\n    position: absolute;\n    color: white;\n    top: 3px;\n    left: 12px;\n    font-size: 18px;\n    user-select: none;\n}\n\n.add-new-text {\n    position: absolute;\n    left: 32px;\n    top: 7px;\n    font-size: 13px;\n    font-weight: 500;\n    user-select: none;\n}\n\n.new-variable-button {\n    position: absolute;\n    left: 866px;\n    top: 118px;\n    font-size: 14px;\n    background: #007c77 0% 0% no-repeat padding-box;\n    border-radius: 0px 4px 4px 0px;\n    width: 101px;\n    height: 32px;\n    font-family: \"Roboto\";\n    border: 0px solid;\n    color: white;\n    padding: 0px;\n    user-select: none;\n}\n\n.disabled {\n    opacity: 0.5;\n    user-select: none;\n    pointer-events: none;\n    cursor: default;\n}\n\n.data-table {\n    position: absolute;\n    top: 182px;\n    left: 32px;\n    width: 936px;\n    height: 556px;\n    overflow-y: auto;\n    overflow-x: hidden;\n}\n\n.operation-icon {\n    color: #212121;\n    font-size: 20px;\n    cursor: pointer;\n}\n</style>\n"],"sourceRoot":""}]);
